<template>
  <div id="wrap">
    <section-title sec-title="菜园展示"></section-title>
    <div class="content">
      <div class="form">
        <div class="head-bar">
          <span>菜园信息表</span>
        </div>
        <ul class="title">
          <li>用户ID</li>
          <li>用户名</li>
          <li>菜园编号</li>
          <li>菜园名称</li>
          <li>类型</li>
          <li>租地日期</li>
          <li>菜园信息</li>
        </ul>
        <ul class="bar" v-for="n in 10">
          <li>1</li>
          <li>小小123</li>
          <li>2</li>
          <li>小小菜园</li>
          <li>全管理</li>
          <li>2017/5/1-2017/11/1</li>
          <li><a href="javascript:;" @click="show">编辑</a></li>
        </ul>
      </div>
      <div
        class="details"
        :class="{ 'display-block': isShow }">
        <div class="head-bar" v-drag>
          <span>菜园信息编辑</span>
          <a
            href="javascript:;"
            class="close"
            @click="hide">关闭</a>
        </div>
        <div class="body-bar">
          <img-del class="img-list"></img-del>
          <img-del class="img-list"></img-del>
          <img-del class="img-list"></img-del>
          <img-del class="img-list"></img-del>
          <img-add class="img-list"></img-add>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import sectionTitle from '../others/section-title'
  import imgDel from '../img/img-del'
  import imgAdd from '../img/img-add'
  export default {
    name: 'show',
    components: {
      sectionTitle,
      imgDel,
      imgAdd
    },
    data () {
      return {
        isShow: false
      }
    },
    methods: {
      show(){
        this.isShow = true
      },
      hide(){
        this.isShow = false
      }
    }

  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #wrap{
    position: relative;
    background-color: #fff;
    width: 1030px;
    height: 968px;
    margin: 22px 0 0 26px;
  }
  .content{
    margin-top: 40px;
    position: relative;
  }
  .form{
    width: 900px;
    margin: 0 auto;
    box-sizing: border-box;
  }
  .head-bar{
    height: 50px;
    line-height: 50px;
    background-color: #00ca79;
  }
  span{
    font-size: 18px;
    color: #fff;
    margin-left: 20px;
    font-weight: bold;
  }
  .add{
    float: right;
    margin-right: 20px;
    padding: 0px 10px;
    color: #fff;
    font-weight: bold;
  }
  ul{
    width: 100%;
    height: 40px;
    line-height: 40px;
    text-align: center;
  }
  li{
    box-sizing: border-box;
    float: left;
    width: 14.28%;
    border-right: 1px solid #999;
    border-bottom: 1px solid #999;
  }
  input{
    width: 95%;
    height: 100%;
    border: none;
    outline: none;
    background: #fff;
    color: #000;
  }
  select{
    border: none;
    color: #000;
  }
  ul a{
    padding: 2px 4px;
    background-color: #00ca79;
    color: #fff;
    border-radius: 4px;
  }
  ul li:first-child{
    border-left: 1px solid #999;
  }
  ul li:nth-of-type(1),ul li:nth-of-type(2),ul li:nth-of-type(3),ul li:nth-of-type(4),ul li:nth-of-type(5){
    width: 108px;
  }
  ul li:nth-of-type(6),ul li:nth-of-type(7){
    width: 179px;
  }
  .details{
    display: none;
    box-sizing: border-box;
    width: 600px;
    height: 400px;
    border: 6px solid #00ca79;
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: 20px;
    transform: translate(-50%);
    box-shadow: 0 0 10px 2px rgba(0,0,0,0.2);
  }
  .head-bar a{
    float: right;
    color: #fff;
    margin-right: 20px;
  }
  .body-bar{
    width: 100%;
    height: 338px;
    overflow: auto;
  }
  .img-list{
    margin: 20px 40px;
  }
</style>



